import {
  useLoaderData,
  Link,
  useSubmit,
  Form,
  Outlet,
  useLocation,
} from "@remix-run/react";
import connectDb from "~/db/connectDb.server.js";
//import { requireUserSession } from "~/sessions.server";

export async function loader({ request }) {
  const db = await connectDb();
  //const session = await requireUserSession(request);
  const url = new URL(request.url);
  const searchQuery = url.searchParams.get("q");
  const sortQuery = url.searchParams.get("s");
  const posts = await db.models.Post.find(
    searchQuery
      ? {
          title: { $regex: new RegExp(searchQuery, "i") },
        }
      : {}
  ).sort(
    sortQuery
      ? { [sortQuery]: -1, title: 1 }
      : {
          title: 1,
        }
  );
  //if (session.has("userId")) {
  return posts;
  // }
}

export default function Index() {
  const posts = useLoaderData();
  const submit = useSubmit();
  const location = useLocation();

  return (
    <div className="sm:flex:none md:flex w-full">
      <div className="shadow-lg bg-gray-100 mr-5 rounded-2xl p-10  sm:w-full md:w-1/4">
        <h1 className="text-2xl mb-4">Code posts</h1>
        <h2 className="text-lg mb-3">Select code post:</h2>

        <ul className="">
          {posts.map((post) => {
            return (
              <li
                className="text-blue-600 hover:underline  py-2 text-xl"
                key={post._id}
              >
                <Link
                  to={`/posts/${post._id}${location.search}`}
                  className="focus:font-bold p-1"
                >
                  {post.title}
                </Link>
              </li>
            );
          })}
        </ul>
      </div>
      <div className="shadow-lg bg-gray-100 rounded-2xl p-10 sm:w-full md:w-3/4">
        <Outlet />
      </div>
      <Form name="" id="" method="post" action="/logout">
        <button className="w-20 inline-block font-bold bg-gray-200 rounded p-2 text-sm text-red-500 hover:text-red-800">
          Logout
        </button>
      </Form>
    </div>
  );
}
